import React from "react";


/**interface****************************/
//
export interface BidItem {
    amount: number
    maxAmount: number
    price: number
    name: string
    saleAmount: number
    status: BidStatus
    clients: Client[]
}
//
export interface Client {
    name: string
    amount: number
}
//api 
export interface ApiConfig{
    url: string
    method: httpMethod
}

//  
export interface iBidItem{
    amount: number
    maxAmount: number
    price: number
    name: string
}

/**enum****************************/
export enum BidStatus{
    ready,
    sale,
    end
}

export enum httpMethod{
    get = "get",
    post = "post"
}

/**type****************************/
export type setState<T> = React.Dispatch<React.SetStateAction<T>>

export interface SSESender {
    type: SSEType
    data?: any
}

export enum SSEType {
    // 
    session = "session",
    // 
    startSale = "startSale",
    endSale = "endSale",

    // 
    sale = "sale",
    // 
    saleClient = "saleClient",
    setItems = "setItems"
}
